"use client"

import React from "react"
import { useState, useEffect } from "react"
import { Shield, Copy, ArrowLeft } from "lucide-react"
import { verifyEmailCode } from "@/actions/verifyEmailCode"
import { resendEmailCode } from "@/actions/resend_otp"


export default function TwoFactorForm() {
  const [code, setCode] = useState(["", "", "", "", "", ""])
  const [timeLeft, setTimeLeft] = useState(300) // 5 minutes
  const [isExpired, setIsExpired] = useState(false)
  const [isResending, setIsResending] = useState(false)
  const [resendCountdown, setResendCountdown] = useState(0)
  const [isVerifying, setIsVerifying] = useState(false)
  const [verified, setVerified] = useState(false)
  const inputRefs = Array.from({ length: 6 }, () => React.createRef<HTMLInputElement>())

  // Timer countdown
  useEffect(() => {
    if (timeLeft <= 0) {
      setIsExpired(true)
      return
    }

    const interval = setInterval(() => {
      setTimeLeft((prev) => prev - 1)
    }, 1000)

    return () => clearInterval(interval)
  }, [timeLeft])

  // Resend countdown
  useEffect(() => {
    if (resendCountdown <= 0) return

    const interval = setInterval(() => {
      setResendCountdown((prev) => prev - 1)
    }, 1000)

    return () => clearInterval(interval)
  }, [resendCountdown])

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins}:${secs.toString().padStart(2, "0")}`
  }

  const handleDigitChange = (index: number, value: string) => {
    // Only allow single digit
    if (value.length > 1) value = value.slice(-1)
    if (!/^\d*$/.test(value)) return

    const newCode = [...code]
    newCode[index] = value

    setCode(newCode)

    // Auto-focus next input if digit entered
    if (value && index < 5) {
      inputRefs[index + 1].current?.focus()
    }
  }

  const handleKeyDown = (index: number, e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Backspace" && !code[index] && index > 0) {
      inputRefs[index - 1].current?.focus()
    }
  }

  const handleResendCode = async () => {
    setIsResending(true)
    await new Promise((resolve) => setTimeout(resolve, 1000))
    setIsResending(false)
    setTimeLeft(300)
    setIsExpired(false)
    setCode(["", "", "", "", "", ""])
    setResendCountdown(60)
  }

  const handleVerify = async () => {
    if (code.join("").length !== 6) return

    setIsVerifying(true)
    await new Promise((resolve) => setTimeout(resolve, 1500))
    setIsVerifying(false)
    setVerified(true)
  }

  if (verified) {
    return (
      <div className="w-full max-w-md">
        <div className="bg-white rounded-lg shadow-lg p-8 text-center space-y-4">
          <div className="flex justify-center">
            <div className="p-3 bg-[#1e40af] rounded-full">
              <Shield className="w-8 h-8 text-white" />
            </div>
          </div>
          <h2 className="text-2xl font-bold text-gray-900">Vérification réussie!</h2>
          <p className="text-gray-600">Votre authentification à deux facteurs est confirmée.</p>
        </div>
      </div>
    )
  }

  return (
    <div className="w-full max-w-md">
      <div className="bg-white rounded-lg shadow-lg p-8 space-y-6">
        {/* Header with Icon */}
        <div className="flex flex-col items-center space-y-4">
          <div className="p-3 bg-[#1e40af] rounded-full">
            <Shield className="w-8 h-8 text-white" />
          </div>
          <div className="text-center space-y-1">
            <h1 className="text-2xl font-bold text-gray-900">Vérification par e-mail</h1>
            <p className="text-sm text-gray-600">Entrez le code à 6 chiffres envoyé à l'adresse e-mail</p>
          </div>
        </div>

        {/* Code Input Fields */}
        <div className="flex justify-center gap-2">
          {code.map((digit, index) => (
            <input
              key={index}
              ref={inputRefs[index]}
              type="text"
              inputMode="numeric"
              maxLength={1}
              value={digit}
              onChange={(e) => handleDigitChange(index, e.target.value)}
              onKeyDown={(e) => handleKeyDown(index, e)}
              disabled={isExpired || isVerifying}
              className="w-12 h-12 border-2 border-gray-300 rounded-lg text-center text-lg font-semibold focus:border-[#1e40af] focus:outline-none disabled:bg-gray-100 disabled:cursor-not-allowed transition-colors"
            />
          ))}
        </div>

        {/* Hidden dots indicator */}
        <div className="flex justify-center text-gray-400 text-sm tracking-widest">
          {code.map((_, i) => (
            <span key={i}>•</span>
          ))}
        </div>

        {/* Verify Button */}
        <button
          onClick={handleVerify}
          disabled={code.join("").length !== 6 || isExpired || isVerifying}
          className="w-full bg-[#1e40af] hover:bg-[#1e3a8a] disabled:bg-gray-300 text-white font-semibold py-2.5 rounded-lg transition-colors disabled:cursor-not-allowed flex items-center justify-center gap-2"
        >
          <Shield className="w-4 h-4" />
          {isVerifying ? "Vérification..." : "Vérifier le code"}
        </button>

        {/* Expired/Timer Alert */}
        {isExpired ? (
          <div className="p-3 bg-red-50 border border-red-200 rounded-lg">
            <p className="text-sm text-red-700">Le code a expiré. Veuillez demander un nouveau code.</p>
          </div>
        ) : (
          <div className="space-y-2">
            <div className="flex items-center justify-between text-sm text-gray-600">
              <span>Renvoyer le code dans</span>
              <span className="font-semibold text-[#1e40af]">{formatTime(timeLeft)}</span>
            </div>
            {/* Progress bar */}
            <div className="w-full bg-gray-200 rounded-full h-1.5 overflow-hidden">
              <div
                className="bg-[#1e40af] h-full transition-all duration-1000"
                style={{ width: `${(timeLeft / 300) * 100}%` }}
              />
            </div>
          </div>
        )}

        {/* Code Not Received */}
        <button className="text-sm text-gray-700 hover:text-[#1e40af] font-medium transition-colors flex items-center justify-center gap-1">
          <Copy className="w-4 h-4" />
          Code non reçu?
        </button>

        {/* Resend Button */}
        <button
          onClick={handleResendCode}
          disabled={resendCountdown > 0 || isResending || isExpired}
          className="w-full border-2 border-[#1e40af] text-[#1e40af] hover:bg-[#1e40af] hover:text-white disabled:border-gray-300 disabled:text-gray-300 font-semibold py-2 rounded-lg transition-colors disabled:cursor-not-allowed"
        >
          {isResending
            ? "Envoi en cours..."
            : resendCountdown > 0
              ? `Renvoyer dans ${resendCountdown}s`
              : "Renvoyer le code"}
        </button>

        {/* Security Info Box */}
        <div className="p-3 bg-blue-50 border border-blue-200 rounded-lg">
          <h3 className="text-sm font-semibold text-[#1e40af] mb-2 flex items-center gap-2">
            <Shield className="w-4 h-4" />
            Conseils de sécurité
          </h3>
          <ul className="text-xs text-gray-700 space-y-1">
            <li>• Le code expire dans 5 minutes</li>
            <li>• Vérifiez votre boîte de réception et vos spams</li>
          </ul>
        </div>

        {/* Back to Login Link */}
        <button className="w-full text-sm text-[#1e40af] hover:text-[#1e3a8a] font-medium flex items-center justify-center gap-2 transition-colors">
          <ArrowLeft className="w-4 h-4" />
          Retour à la connexion
        </button>
      </div>
    </div>
  )
}
